import React,{Suspense } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import Spinner from 'react-bootstrap/Spinner';
import '../App.css';

// import DelModal from './DelModal';
// import EntryForm from './EntryForm';
// import ExpenseListView from './expense-list-view';
// import UpdateForm from "./UpdateForm";
const DelModal = React.lazy(() => import('./DelModal'));
const EntryForm = React.lazy(() => import('./EntryForm'));
const ExpenseListView = React.lazy(() => import('./expense-list-view'));
const UpdateForm = React.lazy(() => import('./UpdateForm'));

function ModalForm1() {
  const handleSave = (newExpense) => {
    console.log(newExpense);
  };
 return (
    <>
      <div className="container mx-auto">
        <div id="header1">
          <div>
            <h1 id="main-h1">Office Expense Form</h1>
          </div>

          <div id="main-btn">
          <Suspense fallback={<div id='Spinner'>
    <Spinner animation="grow" size="lg" />
</div>}>
        
            <DelModal/>
            <EntryForm handleSave={handleSave} />
      </Suspense>
          </div>
        </div>
        <Suspense fallback={<div id='Spinner'>
    <Spinner animation="grow" size="lg" />
</div>}>
        
        <ExpenseListView/>
        <UpdateForm/>
      </Suspense>


      </div>
    </>
  );
}

export default ModalForm1;
